import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { DemoComponent } from '../demo/demo.component';

const ngDeclarations: any = [ AppComponent, DemoComponent ];
const ngImports: any = [ BrowserModule ];
const ngProviders: any = [ ];
const ngBootStrap: any = [ AppComponent ];

import { Injectable } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import {
  CloukitToggleModule,
  CloukitToggleComponentThemeDefault,
  CloukitToggleComponentThemeCornered
} from '../index';
import { CloukitThemeService } from '@cloukit/theme';
const prefixAll = (x) => x;

@Injectable()
export class MyThemeService extends CloukitThemeService {
  constructor() {
    super();
    const toggleDefaultTheme = new CloukitToggleComponentThemeDefault();
    const toggleCorneredTheme = new CloukitToggleComponentThemeCornered();
    this.registerPrefixer(prefixAll);
    this.registerComponentTheme('toggle', toggleDefaultTheme);
    this.registerComponentTheme('toggle--cornered', toggleCorneredTheme);
  }
};

// ngDeclarations
ngImports.push(ReactiveFormsModule);
ngImports.push(CloukitToggleModule);
ngProviders.push({ provide: CloukitThemeService, useClass: MyThemeService });
// ngBootStrap



@NgModule({
  declarations: ngDeclarations,
  imports: ngImports,
  providers: ngProviders,
  bootstrap: ngBootStrap
})
export class AppModule { }



